import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/java',
      name: 'HelloWorld',
      component: () => import(/* webpackChunkName: 'hello' */'@/view/HelloWorld')
    }
  ]
})



